<template>
  <div id="editor">
    <el-container>
      <el-header height="36px">
        <el-button-group>
          <el-button
            type="primary"
            size="medium"
            class="el-icon-top-left"
            @click="handleImportButton"
          >
            
            <fileLoader id="fileLoader" @loadFile="loadBpmnFromFile" />
          </el-button>
          <el-dropdown>
            <el-button type="primary" size="medium">
              
              <i class="el-icon-bottom-right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <a @click="downloadBpmnToFile">.bpmn</a>
              </el-dropdown-item>
              <el-dropdown-item>
                <a @click="downloadBpmnToSVG">.svg</a>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-button-group>
        <el-button-group>
          <el-button
            type="primary"
            size="medium"
            class="el-icon-magic-stick"
            @click="textTransform"
          >NLP</el-button>
        </el-button-group>
        <!--style="margin-left:10px"-->
        <el-button-group>
          <el-button type="primary" size="medium" @click="resetBPMN"></el-button>
          <el-button type="primary" size="medium" @click="checkBPMN"></el-button>
          <el-button type="primary" size="medium" @click="saveBPMN"></el-button>
          <el-button type="primary" size="medium" @click="recoBPMN"></el-button>
          <el-button type="primary" size="medium" @click="showWindow"></el-button>
          <el-button type="primary" size="medium" @click="showUploadWindow"></el-button>
        </el-button-group>
      </el-header>
      <el-main>

        <div v-show="isShowSearchWindow">
    <Search></Search>
  </div>
  <div v-show="isShowUploadWindow">
    <FileUpload></FileUpload>
    </div>

        <div class="bpmnContainer">
          <div id="BpmnCanvas" ref="canvas"></div>
          <div id="BpmnProperties" ref="canvas"></div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>



<script>
import BpmnModeler from "bpmn-js/lib/Modeler";
//
import propertiesPanelModule from 'bpmn-js-properties-panel'
import propertiesProviderModule from 'bpmn-js-properties-panel/lib/provider/camunda'
import Search from  '../components/Search'
import FileUpload from '../components/FileUpload'
//
import fileLoader from "../components/Editor/fileLoader.vue";
import {request} from '../network/request'
//
import lintModule from "bpmn-js-bpmnlint";
import bpmnlintConfig from "../bpmn/lint/.bpmnlintrc";
//Bpmn
import BlankStr from "../bpmn/samples/blank.js";
import  qs from 'qs';
export default {
  name: "Editor",
  data: function() {
    return {
      xmlStr: this.$store.state.BpmnXml,
      isCollapse: false,
      bpmnModeler: null,
      container: null,
      isShowSearchWindow:false,
      isShowUploadWindow:false
    };
  },
  components: {
    fileLoader: fileLoader,
    Search,FileUpload
  },

  methods: {

    showUploadWindow(){
  this.isShowUploadWindow = !this.isShowUploadWindow
  this.isShowSearchWindow = false
},
showSearch(){
  this.isShowSearchWindow = !this.isShowSearchWindow
  this.isShowUploadWindow = false
},
     showWindow() {
       //const axios = this.$axios;
          //axios.defaults.withCredentials = true
        this.$prompt('', ':', {
            confirmButtonText: '',
            cancelButtonText: '',
            closeOnClickModal: false,
            showInput: true,
            inputPlaceholder: "",
            inputType: "textarea"
        }).then(({value}) => {
            this.$message({
                type: 'success',
                message: ': ' + (value == null ? "" : value)
            })
            //
            request({
              
                url: "/recommendation/RecommSemantics",
                method: "get",
                params: {
                    message: value
                }
            }).then(res => {
                //console.log(res.data)
                this.xmlStr = res.data;
                this.bpmnModeler.importXML(this.xmlStr);
            }).catch(err => {
                console.log("")
            })
        }).catch(() => {
            this.$message({
                type: 'info',
                message: ''
            });
        })
    },
    // 
    initBpmn: function() {
      this.bpmnModeler = new BpmnModeler({
        container: "#BpmnCanvas", //
        propertiesPanel: {
          parent: '#BpmnProperties'//
        },
        additionalModules: [
          lintModule,//
          //
          propertiesPanelModule,
          propertiesProviderModule
        ],
        linting: {
          bpmnlint: bpmnlintConfig
          // active: getUrlParam('linting')
        }
      });
      this.bpmnModeler.importXML(this.xmlStr); //
    },

    // 
    //click()fileLoader
    handleImportButton: function() {
      const element = document.getElementById("fileLoader");
      element.click();
    },
    //loadFile
    //loadFileloadBpmnFromFile
    loadBpmnFromFile: function(bpmnFile) {
      this.xmlStr = bpmnFile;
      this.bpmnModeler.importXML(this.xmlStr);
    },

    //BPMN.BPMNxml
    downloadBpmnToFile: function() {
      this.bpmnModeler.saveXML(
        {
          format: true
        },
        (err, xml) => {
          this.download("1.bpmn", xml);
        }
      );
    },
    //BPMN.SVG
    downloadBpmnToSVG: function() {
      this.bpmnModeler.saveSVG(
        {
          format: true
        },
        (err, data) => {
          this.download("bpmn.svg", data);
        }
      );
    },
    //
    download: function(filename, text) {
      let element = document.createElement("a");
      element.style.display = "none";
      element.setAttribute(
        "href",
        "data:text/plain;charset=utf-8," + encodeURIComponent(text)
      );
      element.setAttribute("download", filename);
      document.body.appendChild(element);
      element.click();
      document.body.removeChild(element);
    },

    //
    //this.xmlStr
    resetBPMN: function() {
      this.xmlStr = BlankStr;
      this.bpmnModeler.importXML(this.xmlStr);
    },

    //
          saveBPMN: function() {            
            
            this.bpmnModeler.saveXML(
              {
                format: true
              },
              (err, xml) => {
                this.xmlStr = xml;
                this.$store.dispatch("editXml", this.xmlStr);
                
                this.$prompt('', ':', {
            confirmButtonText: '',
            cancelButtonText: '',
            closeOnClickModal: false,
            showInput: true,
            inputPlaceholder: "",
            inputType: "textarea"
        }).then(({value}) => {            
            //
            request({
              
                url: "/recommendation/saveBPMN",
                method: "post",
                /*params: {
                    name:value,
                   //message : xml
                },*/
                data:  qs.stringify({name:value,message :xml}),
                
                
            }).then(res => {
                console.log(res.data)
                if(res.data==="0"){
                  this.$message({
                      type: 'success',
                      message: ''
            });
                }
                //this.xmlStr = res.data;
                //this.bpmnModeler.importXML(this.xmlStr);
            }).catch(err => {
                console.log("")
            })
        }).catch(() => {
            this.$message({
                type: 'info',
                message: ''
            });
        })

              }
            );
          },

     //     
     
        recoBPMN: function() {         
          const axios = this.$axios;
          axios.defaults.withCredentials = true
          var strXML;
          //Vue.prototype.$qs = qs         
          this.bpmnModeler.saveXML(
              {
                format: true
              },
              (err, xml) => {
                strXML= xml                      
              }
            );        
       axios({
              method: "post",              
              //url: "http://lxc-backend-java.ingress.isa.buaanlsde.cn/recommendation/RecommStructure",   
              //url: "http://backend-java:8090/recommendation/RecommStructure",   
              url: "http://localhost:8090/recommendation/RecommStructure",        
              data: qs.stringify({lxccontent:strXML})              
            })
              .then(res => {
                //console.log(content);
                this.xmlStr = res.data;
                this.bpmnModeler.importXML(this.xmlStr);
               
               
              })
         },

    //
    checkBPMN: function() {
      /*Toggle Lint*/
      const bjslButton = document.querySelector(".bjsl-button");
      bjslButton.click();
    },
    //
    textTransform: function() {
      const _bpmnModeler = this.bpmnModeler;
      const axios = this.$axios;
      this.$msgbox({
        title: "NLP",
        message: "",
        showInput: true,
        inputType: "textarea",
        confirmButtonText: "BPMN",
        beforeClose: (action, instance, done) => {
          //
          if (action === "confirm") {
            instance.confirmButtonLoading = true;
            instance.confirmButtonText = "...";
            //
            let _param = new URLSearchParams();
            _param.append(
              "str",
              instance.inputValue ? instance.inputValue : " "
            );
            // _param.append("name", "cbaymax")

            axios({
              method: "post",
              // test url: 'http://123.207.143.93:8081/anything',
              url: "/api/text2bpmn",
              data: _param
            })
              .then(res => {
                console.log(res);
                _bpmnModeler.importXML(res.data);
                instance.confirmButtonLoading = false;
                this.$message({
                  type: "success",
                  message: ""
                });
                done();
              })
              .catch(err => {
                instance.confirmButtonLoading = false;
                this.$message({
                  type: "error",
                  message: err
                });
                done();
              });
          } else {
            this.$message({
              type: "info",
              message: ""
            });
            done();
          }
        }
      });
    }
  },

  // - , Vue  DOM created()
  mounted: function() {
    this.initBpmn();
  },
  activated: function() {
    this.xmlStr = this.$store.state.BpmnXml;
    this.bpmnModeler.importXML(this.xmlStr);
  }
};
</script>

<style>
.el-button {
  background-color: #3F4254;
}
#editor .el-container {
  position: fixed;
  margin: auto;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 100%;
}

#editor .el-header {
  display: inline-flex;
  height: 00px;
  padding: 00px;
}

#editor .el-header .el-button-group {
  display: inherit;
}
.el-main {
    
    padding: 00px;
}
#editor .bpmnContainer {
  position: relative;
  margin: auto;
  left: 1px;
  right: 1px;
  bottom: 0px;
  height: 100%;
  border-style: solid;
  border-width: 1px;
}

#editor #BpmnCanvas {
  /*  */
  width: 100%;
  height: 100%;
}
#editor #BpmnProperties {
  /*  */
  position:absolute;
  top:10px;
  right:0px;
  width:00px;
  height:0px;
  overflow-y:scroll;
  background-color: rgba(0, 0, 0, 0);
}

@import "~bpmn-js/dist/assets/diagram-js.css";
@import "~bpmn-js/dist/assets/bpmn-font/css/bpmn.css";
@import "~bpmn-js/dist/assets/bpmn-font/css/bpmn-embedded.css";
@import "~bpmn-js-bpmnlint/dist/assets/css/bpmn-js-bpmnlint.css";
@import '~bpmn-js-properties-panel/dist/assets/bpmn-js-properties-panel.css';
</style>
